.title-container {
    display: flex;
    justify-content: space-between
}

.picture {
    display: block;
    max-height: 512px;
    max-width: 100%;
}
.pic-container {
    display: flex;
    justify-content: center;
    padding: 20px;
    background: white;
}
.posted-by-container {
    padding:10px 0px 0 0;
}
.button-container {
    display: flex;
    justify-content: flex-start;
}
.button-container button {
    background: transparent;
    border: 0px;
    width: 10vw;
    padding: 5px;
    transition: 0.3s;
}
.button-container button:hover {
    background: lightgray;
    cursor: pointer;
}
.emoji div{
    position: absolute;
}
.custom-ui {
    padding: 20px
}
.update-ui {
    background: white
}
#confirm-container {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
}
.confirm-button {
    margin-left: 10px;
}