.create-container {
      margin: 0 auto;
      background: white;
      border-radius: 4px;
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 20px;
      margin-top: 5%;

}
h2 {
      color: #0079d3;
      font-family: sans-serif;
}
#title {
      background-color: transparent;
      border: 1px solid gray;
      width: 100%;
      padding: 5px;
      border-radius: 4px;
}
.button {
      border: 1px solid #0079d3;
      font-weight: bold;
      font-size: 1em;
      padding: 10px;
      margin-top: 5%;
      transition: 0.3s;
      border-radius: 4px;
}
#cancel {
      background: transparent;
      color: #007bff;
      margin-right: 10px;
}
#submit {
      background: #007bff;
      color: white;
}

#cancel:hover {
      filter:brightness(80%);
      cursor: pointer;
}
#submit:hover {
      filter: brightness(90%);
      cursor: pointer;
}

#button-container {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      
}
#photo {
      width: 100%;
      height: 25vw;
      padding-top: 15%;
      text-align:center;
      font-family: sans-serif;
      font-size: 1.5em;
      color: #0079d3;
      margin-top: 20px;
      border-radius:4px;
}
.link {
      display: flex;
      align-items: flex-end;
}
.link:hover {
      text-decoration: none;
}
.upload {
      position: relative;
      width: 200px;
      height: 200px;
      border-width: 2px;
      border-color: rgb(102, 102, 102);
      border-style: dashed;
      border-radius: 5px;
      width: 100%;
      max-height: 512px;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
}
.upload-form{
      width: 80%;
}
.preview-image {
      max-height: 512px;
      max-width: 100%;
}